import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {AddFaqComponent} from './add-faq/add-faq.component';
import {EditFaqComponent} from './edit-faq/edit-faq.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FAQComponent} from './faq.component';
import {LoaderModule} from '../../shared/loader/loader.module';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';

export const routes: Routes = [
    {path: 'create', component: AddFaqComponent},
    {path: 'edit/:id', component: EditFaqComponent},
    {path: '', redirectTo: '/app/faqs/list', pathMatch: 'full'},
    {path: 'list', component: FAQComponent},
];

@NgModule({
    declarations: [AddFaqComponent, EditFaqComponent, FAQComponent],
    imports: [
        CommonModule,
        PerfectScrollbarModule,
        RouterModule.forChild(routes),
        FormsModule,
        ReactiveFormsModule,
        LoaderModule,
        CustomErrorModule
    ]
})
export class FaqModule {
}
