import {Component, OnInit, ViewEncapsulation, ChangeDetectorRef} from '@angular/core';
import {FormGroup, FormControl, FormBuilder, Validators} from '@angular/forms';
import {Router, ActivatedRoute} from '@angular/router';
import {FaqService} from '../../../services/faq.service';
import {IFaqs} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';

@Component({
    selector: 'app-add',
    templateUrl: './add-faq.component.html',
    styleUrls: ['./add-faq.component.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [FaqService]
})
export class AddFaqComponent implements OnInit {

    public faqs: IFaqs[] = [];
    public faq: IFaqs;
    public selected = [];
    public NewFaqForm: FormGroup;
    errorMessage: any[];
    loading: boolean;


    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private faqService: FaqService
    ) {
    }

    ngOnInit() {
        this.buildForm();
    }

    buildForm() {
        this.NewFaqForm = this.formBuilder.group({
            question: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            answer: ['', Validators.compose([Validators.required])],
        })
    }

    onSubmit({value, valid}: { value: IFaqs, valid: boolean }) {
        this.loading = true;
        if (value) {
            this.faqService.addFaq(value).subscribe((faqs: IFaqs) => {
                if (faqs) {
                    Constants.succeeded('Faq created successfully');
                    this.NewFaqForm.reset();
                }
                this.loading = false;
            }, (err) => {
                Constants.handleError(err, this.router, true);
                this.loading = false;
            })
        }

    }


}
