import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {Constants} from '../../../app.constants';
import {IFaqs} from '../../../interfaces/interfaces';
import {FaqService} from '../../../services/faq.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit-faq.component.html',
  styleUrls: ['./edit-faq.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class EditFaqComponent implements OnInit {
  public faqForm: FormGroup;
  faq: IFaqs = null;
  faqs: IFaqs[] = [];
  errorMessage: any;
  loading: boolean;
  pageLoading: boolean;
  onSuccess: any;
  deleteMessageEnabled: boolean;

  constructor(private formBuilder: FormBuilder,
              private router: Router,
              private route: ActivatedRoute,
              private faqService: FaqService) {
  }

  ngOnInit() {
    this.buildForm();
    const id = this.route.snapshot.params['id'];
    this.getFaq(id);

  }

  buildForm() {
    this.faqForm = this.formBuilder.group({
      question: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
      answer: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
    })
  }

  getFaq(id: number) {
    this.pageLoading = true;
    this.faqService.getFaqByID(id).subscribe((faq: IFaqs) => {
      this.pageLoading = false;
      this.faq = faq;
      this.faqForm.patchValue({
        question: this.faq.question,
        answer: this.faq.answer,
      });
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
      this.pageLoading = false
    });
  }


  onSubmit({value, valid}: { value: IFaqs, valid: boolean }) {
    value.id = this.faq.id;
    this.loading = true;
    if (value.id) {
      this.faqService.updateFaq(value).subscribe((faq: any) => {
        this.loading = false;
        if (this.faq) {
          Constants.succeeded('FAQ Has been Updated!');
          window.setTimeout(function () {
            location.reload();
          }, 2000)
        }
      }, (err) => {
        Constants.handleError(err, this.router, true);
        this.loading = false;
      })
    }
  }

  onDelete(event: Event) {
    event.preventDefault();
    Constants.confirm({message: 'Do you want to delete this record?', confirmText: 'Delete'}, (willDelete) => {
      if (willDelete.value) {
        this.deleteMessageEnabled = true;
        this.faqService.deleteFaq(this.faq.id).subscribe((status: any) => {
          if (status) {
            this.router.navigate(['/app/faqs']);
          } else {
            Constants.error('Unable to delete Faq');
          }
          this.deleteMessageEnabled = false;
        }, (err) => {
          Constants.handleError(err, this.router, true);
          this.deleteMessageEnabled = false;
        })
      }
    });
  }


}
