import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
import {IFaqs, PaginatedLog} from '../../interfaces/interfaces';
import {FaqService} from '../../services/faq.service';
import {Constants} from '../../app.constants';
import Swal from 'sweetalert2';
import {AuthService} from '../../services/auth.service';
import {AppData} from '../../data';


@Component({
    selector: 'app-faq',
    templateUrl: './faq.component.html',
    styleUrls: ['./faq.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class FAQComponent implements OnInit {
    public faqs: IFaqs[] = [];
    public faq: IFaqs;
    public searchText: string;
    public p: any;
    IFaqs: any;
    toggle = false;
    selectedAll: any;
    selectedFaq: IFaqs[];
    loading: boolean;
    errorMessage: any;
    public placeHolderMessage = '';
    role = localStorage.getItem('userRole')

    current_page: number;
    data: IFaqs[];
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;

    pfaqs: PaginatedLog;

    constructor(public faqService: FaqService,
                private route: ActivatedRoute, private _Location: Location,
                private router: Router) {
    }

    can(permission) {
        return AuthService.can(permission);
    }

    get permissions() {
        return AppData.permissions;
    }

    ngOnInit() {
        this.getFaqs();
    }

    loadData(search) {
        this.loading = false;
        this.placeHolderMessage = 'Searching........... Please Wait';
        this.faqService.getAllFaqs(search).subscribe((faqs: IFaqs[]) => {
            this.faqs = faqs;
            if (!faqs.length) {
                this.placeHolderMessage = 'No FAQ On The System Yet!';
            } else {
                this.placeHolderMessage = '';
            }
            this.loading = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.loading = false;
        });
    }

    public getFaqs(): void {
        this.loading = true;
        this.faqService.getFaqs().subscribe((faqs: IFaqs[]) => {
            this.faqs = faqs;
            this.loading = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.loading = false;
        });
    }

    deleteFaq(id) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You won\'t be able to revert this!',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.faqService.deleteFaq(id).subscribe((status: boolean) => {
                    if (status) {
                        Constants.succeeded('Faq has been deleted!');
                        window.setTimeout(function () {
                            location.reload();
                        }, 2000)
                    } else {
                        this.errorMessage = 'Unable to delete Faq'
                    }
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                });
            }
        })
    }
}
